<template>
  <div class="goods-list" ref="goodslist" :style="{ height: listHeight }">
    <goods-list-item
      v-for="item in goodsList"
      :key="item.image"
      :prop="item"
      class="item"
      :style="item.style"
      :column="column"
      @load="loadImg"
    ></goods-list-item>
  </div>
</template>

<script>
import GoodsListItem from './GoodsListItem'
import _ from 'lodash'

export default {
  props: {
    // 
    prop: {
      type: Array,
      required: true,
      default() {
        return []
      }
    },
    // 
    column: {
      type: Number,
      default: 2
    }
  },
  data() {
    return {
      contentWitdh: 0, // 
      margin: 10, // 
      listItemStyle: {}, // 
      colunmInfo: [],
      loadImgTotal: 0
    }
  },
  watch: {
    prop(val) {
      this.loadImgTotal = 0
    }
  },
  computed: {
    // ;
    itemWidth() {
      return (this.contentWitdh - this.margin * (this.column + 1)) / this.column
    },
    // 
    goodsList() {
      const colunmInfo = [] // 
      // 
      let list = this.prop
      // 
      if (list.length === 0) {
        return list
      } else {
        // 
        list = list.map((item, index) => {
          // 
          this.getImgWH(item)
          // 32
          const boxHeight = (this.itemWidth / item.imgSize.width) * item.imgSize.height + 32
          if (index < this.column) {
            // 
            item.style = {
              width: this.itemWidth + 'px',
              left: index * (this.itemWidth + this.margin) + this.margin + 'px',
              top: '0px'
            }
            // 
            colunmInfo.push({
              columnHeight: boxHeight + this.margin,
              columnIndex: index
            })
          } else {
            const { columnHeight, columnIndex } = colunmInfo.sort(
              (a, b) => a.columnHeight - b.columnHeight
            )[0]
            // 
            item.style = {
              width: this.itemWidth + 'px',
              left: columnIndex * (this.itemWidth + this.margin) + this.margin + 'px',
              top: columnHeight + 'px'
            }
            // 
            colunmInfo[0].columnHeight += boxHeight + this.margin
          }
          return item
        })
        this.saveColunmInfo(colunmInfo)
        return list
      }
    },
    // 
    listHeight() {
      if (this.colunmInfo.length === 0) {
        return '0px'
      } else {
        const colunmInfoCopy = _.cloneDeep(this.colunmInfo)
        return colunmInfoCopy.sort((a, b) => b.columnHeight - a.columnHeight)[0].columnHeight + 'px'
      }
    }
  },
  components: {
    GoodsListItem
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.resize)
  },
  mounted() {
    this.initData()
    this.total = 0
  },
  activated() {
    this.contentWitdh = this.$refs.goodslist.offsetWidth
  },
  methods: {
    initData() {
      // 
      this.contentWitdh = this.$refs.goodslist.offsetWidth
      const that = this
      // contentWitdh
      this.resize = _.debounce(() => {
        return (() => {
          // window.screenWidth = document.body.clientWidth
          if (this.$refs.goodslist.offsetWidth) {
            that.contentWitdh = this.$refs.goodslist.offsetWidth
          }
        })()
      }, 10)

      window.addEventListener('resize', this.resize)
    },
    // 
    getImgWH(item) {
      if (!item.img) {
        const url = item.show.img
        // url
        const WH = url.split('_')[2].split('.')[0].split('x')
        // 
        item.imgSize = {
          width: WH[0] / 2,
          height: WH[1] / 2
        }
        return
      }
      const url = item.img
      // url
      const WH = url.split('_')[2].split('.')[0].split('x')
      // 
      item.imgSize = {
        width: WH[0] / 2,
        height: WH[1] / 2
      }
    },
    saveColunmInfo(colunmInfo) {
      this.colunmInfo = colunmInfo
    },
    loadImg() {
      if (++this.loadImgTotal === this.prop.length) {
        this.$emit('load')
      }
    }
  }
}
</script>

<style lang='less' scoped>
.goods-list {
  position: relative;
  .item {
    position: absolute;
  }
}
</style>
