<template>
  <div class="goods-list-item">
    <a href="javascript:;" class="goods-list-item-link" @click="goGoods" v-if="prop.img">
      <img :src="prop.img" alt="" class="goods-list-item-img" @load="load" />
      <div class="msg">
        <p class="goods-list-item-title">{{ prop.title }}</p>
        <p class="goods-list-item-msg">
          <span class="price">{{ prop.price }}</span>
          <span class="collect">{{ prop.cfav }}</span>
        </p>
      </div>
    </a>
    <a :href="prop.clienUrl" v-if="prop.show && !prop.img">
      <img :src="prop.show.img" alt="" class="goods-list-item-img" @load="load" />
    </a>
  </div>
</template>

<script>
export default {
  props: {
    prop: {
      type: Object,
      required: true,
      default() {
        return {
          img: '', // 
          title: '', // 
          price: '', // 
          cfav: '' // 
        }
      }
    }
  },
  methods: {
    goGoods() {
      this.$router.push('/detail/' + this.prop.iid)
    },
    load() {
      this.$emit('load')
    }
  }
}
</script>

<style lang='less' scoped>
.goods-list-item {
  .goods-list-item-img {
    width: 100%;
    border-radius: 5px;
  }
  .goods-list-item-title {
    font-size: 14px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    height: 16px;
    line-height: 16px;
  }
  .msg {
    margin-top: 3px;
  }
  .goods-list-item-msg {
    text-align: center;
    font-size: 14px;
    .price {
      color: var(--color-high-text);
    }
    .collect {
      position: relative;
      padding-left: 16px;
      z-index: 0;
    }
    .collect::before {
      content: '';
      position: absolute;
      top: 0;
      left: 2px;
      width: 14px;
      height: 14px;
      background: url('~assets/img/common/collect.svg') no-repeat;
      background-size: 14px;
    }
  }
}
</style>
